
#app {
  /*background: #ffe0b3;*/
  font-family:'Robonto', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box
}
body {
  font-size: 16px;
  font-family: 'Robonto', Arial, sans-serif;
  position: relative;
}
*::before,
*::after {
  box-sizing: border-box;
}
ul, ol {
  list-style: none;
}
a {
  color: #222222;;
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  text-decoration: none !important;
}
input {
  border: none;
  outline: none;
}
.clear {
  clear: both;
}
ins{
    text-decoration: none;
    display: block;
    float: left;
    color: red;
    width: 50%;
}
del{
    display: block;
    float: right;
}



